*,
*::after,
*::before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.white {
  color: white;
}

body {
  width: 100vw;
	height: 100vh;
  font-family: Nunito;
}

main {
	height: 100%;
	background: url(${___CSS_LOADER_URL_REPLACEMENT_5___}) no-repeat center center fixed;
	background-size: cover;
  padding: 1rem 2rem;

	display: flex;
	flex-direction: column;
}

.pad-bottom {
  padding-bottom: 1rem;
}

.search-container {
	position: absolute;
  left: 50%;
  transform: translateX(-50%);
	top: 1rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

#search {
  width: clamp(20rem, 35vw, 50rem);
  border: none;
  border-radius: 6rem;
  padding: 1rem 2rem;
  height: 2rem;
}

.error {
  font-size: .6rem;
  color: red;
  font-style: italic;
  position: absolute;
  top: 2.3rem;
  letter-spacing: .2rem;
}

.search-container::before {
  content: '\\f002';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: .5rem;
  color: black;
}

.main-display {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.main-info,
.display {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.main-info {
  padding: 1rem;
  align-items: start;
}

.title {
  font-size: 3.5rem;
}

.large {
  font-size: 2rem;
}

.display {
  align-items: center;
}

.underline {
  text-decoration: underline;
}

.temperature-abbreviation {
  border: none;
  background: none;
  cursor: pointer;
}

.weather-icon {
  width: 4rem;
  height: 4rem;
}

.secondary-info > * {
  display: flex;
  padding: 2rem 0;
}

.secondary-info .fa-solid {
  padding-right: 1rem;
  font-size: 3rem;
}

.container {
  display: flex;
  flex-direction: column;
}
